"use client";
import React, { useState, useEffect } from "react";
import Header from "@/components/Header";
import ThreeDModel from "@/components/ThreeDModel";

// TriCoin Hash Algorithm for wallet generation
const PHI = 1.61803398875;

function ts1613Hash(input: string, salt = "", iterations = 1000) {
  let hash = input + salt;
  for (let i = 0; i < iterations; i++) {
    hash = btoa(hash + PHI.toString() + i.toString()).slice(0, 64);
  }
  return hash;
}

export default function HomePage() {
  const [balance, setBalance] = useState(0);
  const [walletAddress, setWalletAddress] = useState("");
  const [timeToListing, setTimeToListing] = useState("");
  const [showWalletModal, setShowWalletModal] = useState(false);

  // Set listing date to future date
  const LISTING_DATE = new Date("2025-03-15T00:00:00");

  useEffect(() => {
    // Load saved wallet and balance
    const savedWallet = localStorage.getItem("tricoin-wallet");
    const savedBalance = localStorage.getItem("tricoin-balance");
    
    if (savedWallet) {
      setWalletAddress(savedWallet);
    }
    if (savedBalance) {
      setBalance(parseFloat(savedBalance));
    }

    // Countdown timer
    const timer = setInterval(() => {
      const now = new Date();
      const timeDiff = LISTING_DATE.getTime() - now.getTime();
      
      if (timeDiff > 0) {
        const days = Math.floor(timeDiff / (1000 * 60 * 60 * 24));
        const hours = Math.floor((timeDiff % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
        const minutes = Math.floor((timeDiff % (1000 * 60 * 60)) / (1000 * 60));
        const seconds = Math.floor((timeDiff % (1000 * 60)) / 1000);
        
        setTimeToListing(`${days}d ${hours}h ${minutes}m ${seconds}s`);
      } else {
        setTimeToListing("LISTED!");
      }
    }, 1000);

    return () => clearInterval(timer);
  }, []);

  const createWallet = () => {
    const seed = Date.now().toString() + Math.random().toString();
    const newWallet = "TC" + ts1613Hash(seed).slice(0, 42);
    setWalletAddress(newWallet);
    localStorage.setItem("tricoin-wallet", newWallet);
    setShowWalletModal(false);
  };

  const addTestBalance = () => {
    const newBalance = balance + 100;
    setBalance(newBalance);
    localStorage.setItem("tricoin-balance", newBalance.toString());
  };

  return (
    <div className="min-h-screen bg-black text-cyan-400" style={{
      backgroundImage: "radial-gradient(circle at center, #001a33 0%, #000 70%)"
    }}>
      <Header />
      
      <main className="container mx-auto px-4 py-16">
        {/* Hero Section */}
        <section className="flex flex-col items-center justify-center text-center mb-20">
          <h1 className="text-5xl md:text-7xl font-bold mb-6" style={{
            fontFamily: "Orbitron, monospace",
            background: "linear-gradient(45deg, #00ffff, #0066ff)",
            WebkitBackgroundClip: "text",
            WebkitTextFillColor: "transparent",
            textShadow: "0 0 30px rgba(0,255,255,0.5)"
          }}>
            TRICOIN
          </h1>
          <p className="text-xl md:text-2xl text-cyan-300 mb-8 max-w-3xl">
            The Divine Cryptocurrency - Powered by TS1613 Sacred Algorithm
          </p>
          
          {/* Countdown Timer */}
          <div className="bg-cyan-900/20 border border-cyan-400 rounded-lg p-6 mb-8 w-full max-w-md">
            <h2 className="text-xl font-bold mb-2 text-cyan-300">Time to Listing</h2>
            <div className="text-3xl font-mono font-bold text-cyan-400 mb-2">
              {timeToListing}
            </div>
            <p className="text-cyan-300">March 15, 2025</p>
          </div>

          <div className="w-full max-w-2xl h-96 mb-8">
            <ThreeDModel />
          </div>
          
          <div className="flex flex-col sm:flex-row gap-4">
            <button 
              onClick={() => setShowWalletModal(true)}
              className="px-8 py-4 rounded-lg font-bold text-lg uppercase tracking-wider transition-all duration-300 bg-gradient-to-r from-blue-600 to-cyan-500 hover:from-cyan-500 hover:to-blue-600 text-black"
              style={{ boxShadow: "0 0 10px #0ff" }}
            >
              Create Wallet
            </button>
            <a href="/miner" className="px-8 py-4 border border-cyan-400 text-cyan-400 rounded-lg font-bold text-lg uppercase tracking-wider hover:bg-cyan-400/10 transition-colors">
              Start Mining
            </a>
          </div>
        </section>

        {/* Wallet Section */}
        {walletAddress && (
          <section className="bg-cyan-900/20 border border-cyan-400 rounded-lg p-6 mb-20">
            <h2 className="text-2xl font-bold mb-4 text-cyan-300">Your TriCoin Wallet</h2>
            <div className="grid md:grid-cols-2 gap-4">
              <div>
                <p className="text-cyan-300 mb-2">Wallet Address:</p>
                <div className="bg-black/50 p-3 rounded border border-cyan-600 font-mono text-sm break-all text-cyan-400">
                  {walletAddress}
                </div>
              </div>
              <div>
                <p className="text-cyan-300 mb-2">Balance:</p>
                <div className="bg-black/50 p-3 rounded border border-cyan-600 text-2xl font-bold text-cyan-400">
                  {balance.toFixed(2)} TRC
                </div>
                <button 
                  onClick={addTestBalance}
                  className="mt-2 px-4 py-2 bg-cyan-600 text-black rounded hover:bg-cyan-500 transition-colors"
                >
                  Add Test Balance (+100 TRC)
                </button>
              </div>
            </div>
          </section>
        )}

        {/* Features Section */}
        <section className="grid md:grid-cols-3 gap-8 mb-20">
          <div className="text-center p-6 bg-cyan-900/20 border border-cyan-400 rounded-lg">
            <div className="w-16 h-16 mx-auto mb-4 bg-cyan-400/20 rounded-full flex items-center justify-center">
              <div className="relative w-8 h-8">
                <div className="absolute top-1/2 left-0 w-full h-0.5 bg-cyan-400 transform -translate-y-1/2"></div>
                <div className="absolute left-1/2 top-0 h-full w-0.5 bg-cyan-400 transform -translate-x-1/2"></div>
              </div>
            </div>
            <h3 className="text-xl font-semibold mb-2 text-cyan-300">Sacred Algorithm</h3>
            <p className="text-cyan-400">
              TS1613 hash algorithm based on divine mathematical principles
            </p>
          </div>
          <div className="text-center p-6 bg-cyan-900/20 border border-cyan-400 rounded-lg">
            <div className="w-16 h-16 mx-auto mb-4 bg-cyan-400/20 rounded-full flex items-center justify-center">
              <div className="relative w-8 h-8">
                <div className="absolute top-1/2 left-0 w-full h-0.5 bg-cyan-400 transform -translate-y-1/2"></div>
                <div className="absolute left-1/2 top-0 h-full w-0.5 bg-cyan-400 transform -translate-x-1/2"></div>
              </div>
            </div>
            <h3 className="text-xl font-semibold mb-2 text-cyan-300">Secure Mining</h3>
            <p className="text-cyan-400">
              Advanced cryptographic security with golden ratio mathematics
            </p>
          </div>
          <div className="text-center p-6 bg-cyan-900/20 border border-cyan-400 rounded-lg">
            <div className="w-16 h-16 mx-auto mb-4 bg-cyan-400/20 rounded-full flex items-center justify-center">
              <div className="relative w-8 h-8">
                <div className="absolute top-1/2 left-0 w-full h-0.5 bg-cyan-400 transform -translate-y-1/2"></div>
                <div className="absolute left-1/2 top-0 h-full w-0.5 bg-cyan-400 transform -translate-x-1/2"></div>
              </div>
            </div>
            <h3 className="text-xl font-semibold mb-2 text-cyan-300">Divine Purpose</h3>
            <p className="text-cyan-400">
              Cryptocurrency with spiritual significance and higher purpose
            </p>
          </div>
        </section>

        {/* Stats Section */}
        <section className="bg-cyan-900/20 border border-cyan-400 rounded-2xl p-8 mb-20">
          <div className="grid md:grid-cols-4 gap-8 text-center">
            <div>
              <div className="text-3xl font-bold text-cyan-400 mb-2">∞</div>
              <div className="text-cyan-300">Divine Supply</div>
            </div>
            <div>
              <div className="text-3xl font-bold text-cyan-400 mb-2">1613</div>
              <div className="text-cyan-300">Hash Length</div>
            </div>
            <div>
              <div className="text-3xl font-bold text-cyan-400 mb-2">φ</div>
              <div className="text-cyan-300">Golden Ratio</div>
            </div>
            <div>
              <div className="text-3xl font-bold text-cyan-400 mb-2">✝</div>
              <div className="text-cyan-300">Sacred Symbol</div>
            </div>
          </div>
        </section>

        {/* CTA Section */}
        <section className="text-center">
          <h2 className="text-3xl md:text-4xl font-bold mb-4 text-cyan-300">
            Join the Divine Revolution
          </h2>
          <p className="text-xl text-cyan-400 mb-8 max-w-2xl mx-auto">
            Be part of the sacred cryptocurrency that bridges technology and spirituality
          </p>
          <a href="/sacred" className="px-8 py-4 bg-gradient-to-r from-blue-600 to-cyan-500 text-black rounded-lg font-bold hover:from-cyan-500 hover:to-blue-600 transition-all duration-300">
            Explore Sacred Matrix
          </a>
        </section>
      </main>

      {/* Wallet Creation Modal */}
      {showWalletModal && (
        <div className="fixed inset-0 bg-black/80 flex items-center justify-center z-50">
          <div className="bg-cyan-900/20 border border-cyan-400 rounded-lg p-8 max-w-md w-full mx-4">
            <h3 className="text-2xl font-bold mb-4 text-cyan-300">Create TriCoin Wallet</h3>
            <p className="text-cyan-400 mb-6">
              Generate a new secure wallet using the TS1613 algorithm
            </p>
            <div className="flex gap-4">
              <button 
                onClick={createWallet}
                className="flex-1 px-4 py-2 bg-gradient-to-r from-blue-600 to-cyan-500 text-black rounded font-bold hover:from-cyan-500 hover:to-blue-600 transition-all"
              >
                Generate Wallet
              </button>
              <button 
                onClick={() => setShowWalletModal(false)}
                className="flex-1 px-4 py-2 border border-cyan-400 text-cyan-400 rounded hover:bg-cyan-400/10 transition-colors"
              >
                Cancel
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Footer */}
      <footer className="border-t border-cyan-400 mt-20 py-8">
        <div className="container mx-auto px-4 text-center text-cyan-400">
          <p>&copy; 2024 TriCoin. Sacred Cryptocurrency for Divine Purpose.</p>
        </div>
      </footer>
    </div>
  );
}
